import React, { useEffect } from "react";
import { Button, Grid } from "@mui/material";
import CreatePlaylistButton from "./CreatePlaylitButton";
import CreatePlaylist from "./flow/flow";
import Divider from '@mui/material/Divider';

function LoginButton() {

  useEffect(() => {
    const querystring = window.location.search;
    const urlParams = new URLSearchParams(querystring);
    const accessToken = urlParams.get('access_token');
    const refreshToken = urlParams.get('refresh_token');

    console.log(accessToken);
    console.log(refreshToken);
    
  }, [])

  const querystring = window.location.search;
  const urlParams = new URLSearchParams(querystring);
  const accessToken = urlParams.get('access_token');

  const buttonSX = {
    mt: 5,
    background: '#1ed760',
    width: 230,
    "&:hover": {
      color: '#1ed760',
      backgroundColor: '#faf9f7'
    },
  }

  const hrLineSX = {
    color: '#faf9f7',
    background: '#faf9f7',
    height: '1px', 
    borderRadius: '15px', 
    mt: 5, 
    ml: '40%',
    mr: '40%', 
    maxWidth: '350px'
  }
  
  return (
    <Grid textAlign='center'>     
      <Button 
      href="https://liked-to-playlist.herokuapp.com/login"
      size="large"
      variant='contained' 
      sx={buttonSX}
      >
        <img 
          className="icon" 
          src={require('./icons/spotify.png')}
          alt="logo"
        >
        </img>
        {accessToken ? 'Logged in' : 'Login to Spotify'}
      </Button>
      <Divider variant="middle" sx={hrLineSX} light></Divider>
      <CreatePlaylistButton
        click={CreatePlaylist}
        name={accessToken ? 'Create Playlist' : 'Please Log in'}
      />
    </Grid>
  );
};

export default LoginButton;